	import ENVIRONMENT_CONFIG from '../../config/envConfig.js'
	import tkiQrcode from '../tki-qrcode/tki-qrcode.vue';
	import {mapState} from 'vuex'
	export default {
		name: 'postModal',
		components: {
			tkiQrcode
		},
		computed: {
			...mapState(['EXPERIENCE','EDUCATION'])
        },
        props:{
            par:{
                type: Object
            }
        },
		data() {
			return {
				position: null,
				positionId: '',
				companyId:'',
				companyName: '',
				val: '',
				cover: ENVIRONMENT_CONFIG.imageUrl,
                jobList:[],
                isShow: false

			};
		},
		methods: {
			qrRPosition(path) {
				let that = this;
				this.qr_path = path;
				let system_info = uni.getSystemInfoSync();
				let ctx = uni.createCanvasContext('firstCanvas');
				uni.getImageInfo({
					src: that.cover,
					success(res) {
						let w = wx.getSystemInfoSync().windowWidth - 20
                        let h = w * 1.45 < 500 ? 500 : w * 1.45
                        console.log(1)
                        ctx.drawImage(res.path, -5, 0, w+10, h)
                        ctx.draw( false, (res)=>{
                            console.log(res, 'img')
                        })
						ctx.font = "25px SourceHanSansCN-Bold,SourceHanSansCN"
						ctx.fillText('悬赏推荐',25,h*0.095)
                        console.log(2)

						ctx.font = "20px SourceHanSansCN-Bold,SourceHanSansCN"
						ctx.fillText('推荐入职奖励最高6000元',25, h*0.17)
                        console.log(3)
                        ctx.draw()
						ctx.font = "25px SourceHanSansCN-Bold,SourceHanSansCN"
						ctx.setFillStyle('rgba(255, 255, 255, 1)')
						ctx.setTextAlign('center')
						ctx.fillText(that.position.positionName,w/2,h*0.34)
						
						ctx.font = "15px SourceHanSansCN-Bold,SourceHanSansCN"
						ctx.fillText(that.position.companyName,w/2,h*0.40)
						ctx.fillText(''+that.EXPERIENCE[that.position.workYearId]
							+' | '+that.EDUCATION[that.position.educationLevelId]
							+' | '+that.position.cityName,w/2,h*0.45)
						ctx.font = "18px SourceHanSansCN-Bold,SourceHanSansCN"
						ctx.fillText(that.position.salaryMin/1000+'-'+that.position.salaryMax/1000+'k',w/2,0.5*h)
                        console.log(4)
						
						ctx.setFillStyle('rgba(42, 39, 31, 1))')
						ctx.fillText('长按识别，了解详情',w/2,h*0.85)
						ctx.drawImage(path, w/2-uni.upx2px(200)/2, 0.55*h,uni.upx2px(200),uni.upx2px(200));
                        console.log(5)
						ctx.draw(false, (res) => {
                            console.log(res, '222')
                        })
					},
					fail(error) {
						console.log(error);
					}
				})
			},
			qrRCompany(path) {
				let that = this;
				this.qr_path = path;
				let system_info = uni.getSystemInfoSync();
				let ctx = uni.createCanvasContext('firstCanvas');
				uni.getImageInfo({
					src: that.cover,
					success(res) {
						let w = wx.getSystemInfoSync().windowWidth - 20
						let h = w * 1.45 < 500 ? 500 : w * 1.45
						ctx.drawImage(res.path, -5, 0, w+10, h)
						ctx.font = "25px SourceHanSansCN-Bold,SourceHanSansCN"
						ctx.fillText('悬赏推荐',25,h*0.095)

						ctx.font = "20px SourceHanSansCN-Bold,SourceHanSansCN"
						ctx.fillText('推荐入职奖励最高6000元',25,h*0.17)

						ctx.font = "25px SourceHanSansCN-Bold,SourceHanSansCN"
						ctx.setFillStyle('rgba(255, 255, 255, 1)')
						ctx.setTextAlign('center')
						ctx.fillText(that.companyName,w/2,h*0.36)
						
						ctx.font = "18px SourceHanSansCN-Bold,SourceHanSansCN"
						var job = ''
						for(let item of that.jobList){
							if(job){
								job += ' · '
							}
							job += item.positionName
						}
						ctx.fillText(job,w/2,h*0.44)
						ctx.setFillStyle('rgba(42, 39, 31, 1))')
						ctx.fillText('长按识别，了解详情',w/2,h*0.85)
						ctx.drawImage(path, w/2-uni.upx2px(200)/2, 0.55*h,uni.upx2px(200),uni.upx2px(200));
						ctx.draw(false, () => {})
					},
					fail(error) {
						console.log(error);
					}
				})
			},
			longPress(){
				let that = this
				wx.showModal({ 
					content:'保存图片至相册？',
					success:function(res){
						if(res.confirm){ //判断用户是否点击了确定
							that.saveImg()
						}
					}
			    })
			},
			saveImg(){
				uni.canvasToTempFilePath({
					x: 0,
					y: 0,
					width: 375,
					height: uni.upx2px(1020),
					destWidth: 375,
					destHeight: uni.upx2px(1020),
					canvasId: 'firstCanvas',
					success: function(res) {
						console.log(res)
						uni.saveImageToPhotosAlbum({
							filePath: res.tempFilePath,
							success: function() {
								uni.navigateBack()
							}
						});
					},
					fail(e) {
						console.log(e);
						uni.showToast({
							title: '生成海报失败',
							icon: 'none'
						});
					}
				})
            },
            show(){
                setTimeout(()=>{
                    this.init()
                }, 1000)
                this.isShow = true
            },
            hide(){
                this.isShow = false
            },
            init() {
                let par = this.par
                this.positionId = par.positionId
                this.companyId = par.companyId
                if(this.positionId){
                    this.val = 'https://host/pages/position/index?companyId='+this.companyId
                    uni.request({
                        url: ENVIRONMENT_CONFIG.host+'/tr/web/talentRecommend/positionInfo?appCode=test',
                        method: 'POST',
                        data: {
                            positionId: this.positionId
                        },
                        success: res => {
                            let realRes = res.data
                            if(realRes.code==0){
                                this.position = realRes.data
                                this.$refs.qrcode1._makeCode();
                            }
                        },
                        fail: () =>{
                            console.log('error')
                        }
                    })
                }else{
                    this.val = 'https://host/pages/company/index?companyId='+this.companyId
                    uni.request({
                        url: ENVIRONMENT_CONFIG.host+'/tr/web/talentRecommend/companyInfo?appCode=test',
                        method: 'POST',
                        data: {
                            companyId: this.companyId,
                        },
                        success: res => {
                            let realRes = res.data
                            if(realRes.code==0){
                                this.companyName = realRes.data.companyName
                                this.$refs.qrcode2._makeCode()
                            }
                        },
                        fail: () =>{
                            console.log('error')
                        }
                    })
                    uni.request({
                        url: ENVIRONMENT_CONFIG.host+'/tr/web/talentRecommend/positionList?appCode=test',
                        method: 'POST',
                        data: {
                            companyId: this.companyId,
                            page: 1,
                            pageSize: 3
                        },
                        success: res => {
                            let realRes = res.data
                            if(realRes.code==0){
                                this.jobList = realRes.data.list
                            }
                        },
                        fail: () =>{
                            console.log('error')
                        }
                    })
                }
            }
		}
	};
